* {
    margin: 0;
    padding: 0;
    font-family: Impact, Haettenschweiler, "Arial Narrow Bold", sans-serif;
  }
  
.home-page {
    height: 100vh;
    width: 100%;
  
    .bg-video {
      height: 100%;
      width: 100%;
      object-fit: cover;
      z-index: -1;
    }
  
    .overlay {
      position: absolute;
      top: 0;
      left: 0;
      height: 100%;
      width: 100%;
      background-color: rgba(0, 0, 0, 0.5);
    }
  
    .welcome-txt {
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      color: white;
      font-size: 2rem;
  
      @media only screen and (max-width: 600px) {
        font-size: 1.5rem;
      }
    }
}